button,
label {
  cursor: pointer;
}

.timer {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #212121;
  border-radius: 2px;
}

.field {
  margin: 10px;
  font-family: 'Roboto Mono', monospace;
  font-size: 30px;
}
.value,
.label {
  display: block;
}
button,
input {
  display: inline-flex;
  margin-left: 4px;
  margin-right: 4px;
  text-transform: uppercase;
  border: 1;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 24px;
  border-radius: 3px;
}
